const RouterUtil = (function () {
  const _title = function (title) {
    document.title = (title || '')
  }
  const _formatUrlParams = function (args) {
    let s = []
    for (let k in args) {
      if (args.hasOwnProperty(k)) {
        s.push(k + '=' + args[k])
      }
    }
    return s.join('&')
  }
  return {
    title: _title,
    formatUrlParams: _formatUrlParams
  }
})()

export default RouterUtil



