export default {
  path: '/',
  name: 'home',
  meta: {
    label: 'Home',
    title: 'Enkel Tools',
    name: ''
  },
  components: {
    HomeRouter: () =>
      import('../components/Home.vue')
  },
  children: [{
    path: '/qrcode',
    name: 'qrcode',
    meta: {
      label: '',
      title: '',
      icon: '#route-qrcode',
      name: '',
      desc: ''
    },
    components: {
      ContentRouter: () =>
        import('../components/tool/Qrcode.vue')
    }
  }, {
    path: '/img',
    name: 'img',
    meta: {
      label: '',
      title: '',
      icon: '#route-img',
      name: '',
      desc: ''
    },
    components: {
      ContentRouter: () =>
        import('../components/tool/Image.vue')
    }
  }, {
    path: '/steganography',
    name: 'steganography',
    meta: {
      label: '',
      title: '',
      icon: '#route-steganography',
      name: '',
      desc: ''
    },
    components: {
      ContentRouter: () => import('../components/tool/Steganography.vue')
    }
  }]
}



