export default {
  path: '/',
  name: 'home',
  meta: {
    label: 'Home',
    title: 'Enkel Tools',
    name: ''
  },
  components: {
    HomeRouter: () =>
      import ('../components/Home.vue')
  },
  children: [{
    path: '/json',
    name: 'json',
    meta: {
      label: 'JSON',
      title: 'JSON',
      icon: '#route-json',
      name: 'JSON',
      desc: 'JSON'
    },
    components: {
      ContentRouter: () =>
        import ('../components/tool/Json.vue')
    }
  }, {
    path: '/url',
    name: 'url',
    meta: {
      label: 'URL',
      title: 'URL',
      icon: '#route-url',
      name: 'URL',
      desc: 'URLURLJSONJSONURL'
    },
    components: {
      ContentRouter: () =>
        import ('../components/tool/Url.vue')
    }
  }, {
    path: '/time',
    name: 'time',
    meta: {
      label: '',
      title: '',
      icon: '#route-time',
      name: '',
      desc: ''
    },
    components: {
      ContentRouter: () =>
        import ('../components/tool/Time.vue')
    }
  }, {
    path: '/diff',
    name: 'diff',
    meta: {
      label: '',
      title: '',
      icon: '#route-diff',
      name: '',
      desc: '/'
    },
    components: {
      ContentRouter: () => import('../components/tool/Diff.vue')
    }
  }, {
    path: '/code',
    name: 'code',
    meta: {
      label: 'CHAR',
      title: 'html',
      icon: '#route-code',
      name: 'html',
      desc: 'html'
    },
    components: {
      ContentRouter: () =>
        import ('../components/tool/Code.vue')
    }
  }]
}


