export default {
  path: '/',
  name: 'home',
  meta: {
    label: 'Home',
    title: 'Enkel Tools',
    name: ''
  },
  components: {
    HomeRouter: () =>
      import ('../components/Home.vue')
  },
  children: [{
    path: '/base64',
    name: 'base64',
    meta: {
      label: 'BASE64',
      title: 'base64',
      icon: '#route-base64',
      name: 'base64',
      desc: 'base64base64'
    },
    components: {
      ContentRouter: () =>
        import ('../components/tool/Base64.vue')
    }
  }, {
    path: '/ip',
    name: 'ip',
    meta: {
      label: 'IP',
      title: 'ip',
      icon: '#route-ip',
      name: 'ip',
      desc: 'IPIP'
    },
    components: {
      ContentRouter: () => import('../components/tool/Ip.vue')
    }
  }, {
    path: '/crypto',
    name: 'crypto',
    meta: {
      label: '',
      title: '',
      icon: '#route-crypto',
      name: '',
      desc: 'MD5AESDESRC4Rabbit'
    },
    components: {
      ContentRouter: () => import('../components/tool/Crypto.vue')
    }
  }]
}


