export default {
  path: '/',
  name: 'home',
  meta: {
    label: 'Home',
    title: 'Enkel Tools',
    name: ''
  },
  components: {
    HomeRouter: () =>
      import('../components/Home.vue')
  },
  children: [
    {
      path: '/scripts',
      name: 'scripts',
      meta: {
        label: '',
        title: '',
        icon: '#route-scripts',
        name: '',
        desc: ''
      },
      components: {
        ContentRouter: () => import('../components/tool/Scripts.vue')
      }
    },
    {
      path: '/augury',
      name: 'augury',
      meta: {
        label: '',
        title: '',
        icon: '#route-augury',
        name: '',
        desc: 'BUGUI'
      },
      components: {
        ContentRouter: () => import('../components/tool/Augury.vue')
      }
    },
    {
      path: '/pinyin',
      name: 'pinyin',
      meta: {
        label: '',
        title: '',
        icon: '#route-pinyin',
        name: '',
        desc: ''
      },
      components: {
        ContentRouter: () =>
          import('../components/tool/Pinyin.vue')
      }
    },
    {
      path: '/morse',
      name: 'morse',
      meta: {
        label: '',
        title: '',
        icon: '#route-morse',
        name: '',
        desc: ''
      },
      components: {
        ContentRouter: () => import('../components/tool/Morse.vue')
      }
    }
  ]
}



